'use strict';
import React from 'react';
import { default as __links } from 'sources/links';

require('styles/pages/contact.less');

const Link = React.createClass({
  render() {
    return (
      <li className="link-item list-item-icon">
        <div className="list-item-icon__icon">
          <a href={ this.props.url } target="_blank">
            <i className={`icon ${this.props.icon}`}></i>
          </a>
        </div>
      </li>
    );
  }
});

export default React.createClass({
  getInitialState() {
    return {
      links: __links,
      loaded: false
    };
  },
  onContainerLoad() {
    if (this.isMounted()) {
      this.setState({ loaded: true });
    }
  },
  componentDidMount() {
    for (let i of __links) {
     let link = ReactDOM.findDOMNode(this.refs[`link-${i}`]);
     link.style.display = 'inline-block';
    }

  },
  render() {
    const links = __links.map((result, i) => {
      return <Link ref={ `link-${i}` } key={ result.name } url={ result.url } icon={ result.icon } />;
    });
    return (
      <div className="links">
        <ul className="link-items list-icons">
          { links }
        </ul>
      </div>
    );
  }
});


